import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { uniqueId } from 'lodash';

import { calibrate, measure } from './crashing_iframe';

class URLInput extends React.Component {
    constructor(props) {
        super(props);
        this.id = uniqueId('urlinput_');
    }

    render() {
        return (
            <div>
                <label htmlFor={this.id}>{this.props.label}</label>
                <input id={this.id} className="url-input" type="text" defaultValue={this.props.url} onChange={this.props.onChange}></input>
                {this.props.children}
            </div>
        );
    }
}

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            allocSize: null,
            calibrationUrl: "https://sheddow.xyz/flask/bytes?len=1000",
            testUrl: "https://sheddow.xyz/flask/bytes?len=1025",
            loadingState: null,
            baseMeasurement: null,
            measurement: null,
        };
    }

    componentDidMount() {
        this.calibrate();
    }

    calibrate = () => {
        this.setState({loadingState: "Calibrating..."});
        calibrate(this.state.calibrationUrl, this.state.allocSize).then(({size, count}) => {
            this.setState({loadingState: null, allocSize: size, baseMeasurement: count});
        });
    }

    measure = () => {
        this.setState({loadingState: "Measuring..."});
        measure(this.state.testUrl, this.state.allocSize).then(count => {
            this.setState({loadingState: null, measurement: count});
        });
    }

    setCalibrationUrl = (e) => {
        this.setState({calibrationUrl: e.target.value});
    }

    setTestUrl = (e) => {
        this.setState({testUrl: e.target.value});
    }

    render() {
        return (
            <div>
                <URLInput label="Base URL" url={this.state.calibrationUrl} onChange={this.setCalibrationUrl}>
                    <span className="measurement">{this.state.baseMeasurement}</span>
                    { 
                        this.state.loadingState ?
                        this.state.loadingState : 
                        <button onClick={this.calibrate}>Calibrate</button>
                    }
                </URLInput>
                <URLInput label="Test URL" url={this.state.testUrl} onChange={this.setTestUrl}>
                    <span className="measurement">{this.state.measurement}</span>
                    { 
                        this.state.loadingState ?
                        this.state.loadingState : 
                        <button onClick={this.measure}>Measure</button>
                    }
                </URLInput>
            </div>
        );
    }
}

// ========================================

ReactDOM.render(
  <App />,
  document.getElementById('root')
);
